'use strict';

/**
 * 
 * @param {string} cmd 
 * @param {boolean} isOutput 
 * @returns {{isError: boolean, response: string}} isErrorresponse
 */
export const runCmd = function (cmd, isOutput = false) {
    let isError = false;
    let response = '';
    try {
        response = eval(cmd);
        if (isOutput) console.log(response);
    }
    catch (ex) {
        isError = true;
        response = ex;
        console.log(ex);
    }
    return {isError, response: String(response)};
};
