const showUserMemoDialog = function () {
    const dialogName = 'userMemoDialog';
    if ($('#' + dialogName).length > 0) return;
    readConfig();
    let bodyContent = `
<p class="font-size-sm"><code>:</code></p>
<div class="form-group">
  <textarea class="form-control" name="userMemoList" rows="13" wrap="off" aria-label="" style="white-space: pre;"></textarea>
</div>`;
    let footerContent = `
<button class="btn btn-primary" type="submit"></button>
<button class="btn btn-secondary" data-dismiss="dialog" type="button"></button>`;
    let $dialog = Dialog.create(dialogName, '', bodyContent, footerContent);
    let $userMemoList = $dialog.find('[name="userMemoList"]');

    $dialog.submit(function (e) {
        e.preventDefault();
        let content = $.trim($userMemoList.val());
        Config.userMemoList = {};
        for (let line of content.split('\n')) {
            line = $.trim(line);
            if (!line) continue;
            if (!/.+?:.+/.test(line)) {
                alert('');
                $userMemoList.focus();
                return;
            }
            let [user, memo = ''] = line.split(':');
            if (!memo) continue;
            Config.userMemoList[user.trim()] = memo.trim();
        }
        writeConfig();
        Dialog.close(dialogName);
    });

    let content = '';
    for (let [user, memo] of Object.entries(Config.userMemoList)) {
        content += `${user}:${memo}\n`;
    }
    Dialog.show(dialogName);
    $userMemoList.val(content).focus();
};

